
"use client"

import { Home, ShoppingCart, Utensils } from "lucide-react"
import Link from "next/link"
import { usePathname } from "next/navigation";

const links = [
  { href: '/admin-panel', label: 'Dashboard Overview', icon: <Home size={18} /> },
  { href: '/admin-panel/orders', label: 'Orders Management', icon: <ShoppingCart size={18} /> },
  { href: '/admin-panel/menu', label: 'Menu Management', icon: <Utensils size={18} /> },
  { href: '/admin-panel/analytics', label: 'Analytics', icon: <BarChart size={18} /> },
  { href: '/admin-panel/settings', label: 'Settings', icon: <Settings size={18} /> },
];
const SideBar = () => {
    const path = usePathname();
    return (
        <div className="basis-[255px] border-r-[1px] border-borderColor p-2 h-[900px] flex flex-col">
            {links.map((link, index) => (
  <Link href={"/dashboard"} className=" p-2 pl-3 btn leading-[22px]  text-white flex font-medium gap-2 items-center ">
                <Home size={16} />
                <span>Dashboard Overview</span>
            </Link>
            ))}
          
        </div>
    )
}

export default SideBar